.container {
    max-width: 1280px;
    padding: 0 20px;
    margin-left: auto;
    margin-right: auto;
}

.fruits-title {
    margin: 0 0 14px 0;
    font-weight: 500;
    font-size: 28px;
    line-height: 1;
    text-align: center;
    text-transform: uppercase;
}
.fruits-text {
    text-align: center;
    margin: 0 0 44px 0;
}
.fruits-list {
    display: flex;
    flex-direction: column;
    gap: 14px;
    margin: 0 0 44px 0;
    padding: 0;    
    list-style: none;
}
.fruits-items {
    max-width: 335px;
    border-radius: 20px;
    padding: 64px 24px 24px 24px;
    transform: scale(1);
    transition: transfom 250ms linear;
}
.fruits-items:hover {
    transform: scale(1.02);
    box-shadow: 0px 0px 12px 5px rgba(84, 3, 3, 0.75);
    -webkit-box-shadow: 0px 0px 12px 5px rgba(84, 3, 3, 0.75);
    -moz-box-shadow: 0px 0px 12px 5px rgba(84, 3, 3, 0.75);
}
.fruits-items:hover .top {
    -webkit-animation: kenburns-top 350ms ease-out both;
    animation: kenburns-top 350ms ease-out both;
}

@keyframes top {
    0% {
        -webkit-transform: scale(1) translateY(0);
        transform: scale(1) translateY(0);
        -webkit-transform-origin: 50% 16%;
        transform-origin: 50% 16%;
    }

    100% {
        -webkit-transform: scale(1.05) translateY(-15px);
        transform: scale(1.05) translateY(-15px);
        -webkit-transform-origin: top;
        transform-origin: top;
    }
}

.fruits-items:nth-last-child(-n + 3) {
    display: none;
}

.strawberry {
    background: #f8766b;
}

.fruitbanana {
    background: #edd251;
}

.watermelon {
    background: #9dc262;
}

.lime {
    background: #bfd663;
}

.apple {
    background: #e93f29;
}

.peach {
    background: #db4f4b;
}

.plum {
    background: #9b5366;
}

.orange {
    background: #f47f4c;
}

