    background: #98b37e;
}
.fruits-items-title {
    margin: 0 0 8px 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.17;
    
}
.fruits-items-text {
    margin: 0 0 4px 0;
}
.fruits-items-img {
    display: block;
    max-width: 100%;
    margin: 0 auto;
}
.fruits-img-container {
    position: relative;
    margin: 0 0 22px 0;
}
.fruits-img-container::after {
    content: '';
    position: absolute;
    left: 17.4%;
    bottom: -7.78%;
    display: block;
    width: 100%;
    max-width: 195px;
    height: 100%;
    max-height: 38px;
    border-radius: 50%;
    background: #000000;
    opacity: 0.1;
    filter: blur(13.5914px);
}
.fruits-items-price {
    position: relative;
    z-index: 1;
    max-width: 126px;
    padding: 12px 0;
    margin: 0;
    border-radius: 200px;
    font-weight: 500;
    text-align: center;
    color: #ae1332;
    background: #ffffff;
}
.btn-order {
    display: flex;
    margin: 0 auto;
    width: 80px;
    height: 80px;
    align-items: end;
    justify-content: center;
    gap: 6px;
    padding: 26px 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.29;
    text-transform: uppercase;
    border-radius: 50%;
    border: none;
    color: #ffffff;
    background: #9dc262;
}

.btn-order:hover,
.btn-order:focus {
    transform: scale(1.02);
    box-shadow: 0px 0px 12px 5px rgba(84, 3, 3, 0.75);
    -webkit-box-shadow: 0px 0px 12px 5px rgba(84, 3, 3, 0.75);
    -moz-box-shadow: 0px 0px 12px 5px rgba(84, 3, 3, 0.75);
    color: #9dc262;
    background: #ffffff;
}
.icon-arrow {
    fill: currentColor;
    width: 14px;
    height: 14px;
}

.btn-order-text {
    transform: rotate(-14deg);
    transform-origin: bottom left;
}
/* RESPONSIVE FOR TABLET */
@media screen and (min-width: 768px) {

    .fruits-title {
        font-size: 40px;
        line-height: 1.1;
    }

    .fruits-text {
        max-width: 547px;
        margin-left: auto;
        margin-right: auto;
    }

    .fruits-list {
        flex-direction: row;
        flex-wrap: wrap;
        gap: 16px;
    }

    .fruits-items {
        flex-basis: calc((100% - 16px) / 2);
        max-width: 344px;
    }

    .fruits-items:nth-last-child(3),
    .fruits-items:nth-last-child(2) {
        display: initial;
    }

    .fruits-items-title {
        font-size: 32px;
        line-height: 1.19;
    }

    .fruits-items-text {
        font-size: 18px;
        line-height: 1.33;
        letter-spacing: -0.02em;
    }

    .fruits-img-container {
        margin: 0 0 6px 0;
    }

    .fruits-items-price {
        max-width: 136px;
        font-size: 18px;
        line-height: 1.33;
    }

    .btn-order {
        width: 114px;
        height: 114px;
        padding: 38px 0;
        gap: 10px;
        font-size: 18px;
        line-height: 1.33;
    }
}

/* RESPONSIVE FOR DESKTOP */
@media screen and (min-width: 1280px) {
    .fruits-list {
        gap: 24px;
    }

    .fruits-items {
        flex-basis: calc((100% - 24px) / 3);
        max-width: 389px;
    }

    .fruits-items:nth-last-child(1) {
        display: initial;
    }
}