        // width: 100vw;
        height: 50px;

    }

    .menu {
        display: flex;
        justify-content: center;
        align-content: center;
        gap: 20px;
        margin-left: 32px;
        margin-right: 94px;
    }

    .header-logo {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 8px;
      
    }

