    }

    .cioncontainer {
        display: none;
    }
    
    .js-menu-container {
        display: none;
    }
}

@media screen and (min-width: 375px) and (max-width: 767px) {

    .headerline {
        min-width: 375px;
        max-width: 767px;
        height: 50px;
    }

    .header-container {
        display: flex;
        flex-direction: row;
        // align-content: center;
        justify-content: space-between;
        // width: 100vw;
        height: 50px;

    }

    .menu {
        display: none;
      
    }

    .header-logo {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 8px;
        margin-left: 32px;
        margin-right: auto;
    }

    .cion {
        display: flex;
        justify-content: flex-end;
        align-content: center;
        margin-right: 32px;
        margin-left: auto;
        margin-top: 10px;
    }

    .cart {
        display: none;
    }

    .cioncontainer {
        display: flex;
        align-items: center;
    }

}
