/*MOBILE STYLING*/

    .js-menu-container {
        min-width: 100vw;
        height: 812px;
    }

    .fh-logo {
        display: flex;
        justify-content: flex-start;
        flex-direction: row;
        width: 100vw;
        margin-left: 20px;
        // margin-left: 0;
        // margin-right: auto;
    }

    .js-close-menu {
        // margin-top: 10px;
        justify-content: end;
    }

    .close-btn {
        margin-right: 13px;
        }

    .mobilemenucontainer {
        width: 100vw;
        background: #FFF;
       
        display: flex;
        justify-content: space-around;
        gap: 155px;
    }

    .mobile-menu-container {
        display: block;
        align-items: center;
        justify-content: space-around;
    }

    .mobile-menu {
        display: block;
        position: fixed;
        top: 0;
        transform: translateX(100%);
        width: 100vw;
        height: 100vh;
        background-color: #AE1332;
        transition-property: transform;
        transition-duration: 250ms;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
        z-index: 1000;
    }

    .mobile-menu.is-open {
        transform: translateX(0);
    }

    .js-close-menu {
  
        width: 24px;
        height: 24px;
        background: transparent;
        border: none;
        display: flex;
        justify-content: center;
    }

    .mobile-navbar {
        width: 100vw;
        margin-top: 124px;
        margin-bottom: 508;
    }

    .mobile-nav-menu {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 16px;
    }

    .mobile-menu-list {
        list-style: none;
        // display: flex;
        // flex-direction: column;
        letter-spacing: -0.56px;
    }

    .a-menu {
        text-decoration: none;
        color: #FFF;
        text-align: center;
        font-family: DM Sans;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 18px;
        /* 128.571% */
        letter-spacing: -0.56px;
    }
